    /* background: linear-gradient(120deg, #52bc22, #24b47e); */
    padding: 20px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 20px auto;
    color: #1b1c22;
    /* color: #24b47e; */
}

.venue {
    color: 1b1c22;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    font-size: 24px;
    font-weight: bold;
    border-radius: 50px;
}

.venue-container {
    width: 40%;
}

.tile>div>.time {
    font-size: 15px;
    text-align: left;
}

.tile>div>.name {
    font-size: 30px;
    text-align: left;
}

.admin-container {
    margin-top: 0px;
}

@media only screen and (max-width: 789px) {
    .venue-main-container {
        margin-top: 50px;
    }

    .admin-container {
        margin-top: 100px;
    }

    .tile {
        padding: 15px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin: 10px auto;
    }

    .venue {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        font-size: 25px;
        font-weight: bold;
        border-radius: 50px;
    }

    .venue-container {
        width: 85%;
    }
}


@media only screen and (max-width: 400px) {
    .tile>div>.time {
        font-size: 15px;
    }

    .tile>div>.name {
        font-size: 25px;
    }

    .venue-container {
        width: 90%;
    }

    .venue {
        font-size: 18px;
    }
}
